import {GetterTree, ActionTree, MutationTree} from 'vuex'
import {RootState} from '~/store'

export const state = () => ({
  info: null,
  userDataGeted: false,
  user_url_list:[]
})

export type UserModuleState = ReturnType<typeof state>

export const getters: GetterTree<UserModuleState, RootState> = {
  // evenMore: state => state.more + 5,
  // nameAndMore: (state, getters, rootState) => `${rootState.name}: ${state.more}`,
  logined: state => !!state.info,
  config: state => state.info ? state.info!['config'] : '{}',
  is_admin: state => state.info && state.info!['email'] === '569552263@qq.com'
}

export const mutations: GetterTree<UserModuleState, RootState> = {
  SET_USER_INFO(state, info) {
    state.info = info
    // if(localStorage) {
    //   localStorage.setItem('vuex_user',JSON.stringify(state))
    // }
  },
  SET_USER_USERDATAGETED(state, info) {
    state.userDataGeted = info
  },
  SET_USER_URL_LIST(state, info) {
    state.user_url_list = info
  },
  // CHANGE_ALL: function (state, all) {
  //   for (const allKey in all) {
  //     // @ts-ignore
  //     state[allKey] = all[allKey]
  //   }
  //   localStorage.setItem('vuex_user', JSON.stringify(state))
  // },
}

export const actions: ActionTree<UserModuleState, RootState> = {
  printRootState({rootState}) {
    // console.log('accessing rootState:', rootState.name)
  },
  async get_user_info({rootState, commit, dispatch}) {
    try {
      // @ts-ignore
      let res = await this.$axios.get(`/xz_server/user/getUserInfo`)
      commit('SET_USER_USERDATAGETED', true)
      commit('SET_USER_INFO', res.data)
      // dispatch('get_user_url_list')
    } catch (e) {
      console.log('SET_USER_INFOe:', e)
    }
  },
  async set_user_config({rootState, dispatch, commit}, config) {
    try {
      if(typeof config !== 'string'){
        config = JSON.stringify(config)
      }
      // @ts-ignore
      let res = await this.$axios.post(`/xz_server/user/setUserConfig`, { config })
      alert('配置已修改')
      dispatch('get_user_info')
    } catch (e) {
      console.log('SET_USER_INFOe:', e)
    }
  },
  async get_user_url_list({commit}) {
    try {
      // @ts-ignore
      const res = await this.$axios.get(`/xz_server/url/getUrlList`)
      commit('SET_USER_URL_LIST', res.data)
    } catch (e) {
      commit('SET_USER_URL_LIST', [])
      console.log('e:', e)
    }
  }

}
