import { GetterTree, ActionTree, MutationTree } from 'vuex'

export const state = () => ({
  search_list:[]
})

export type RootState = ReturnType<typeof state>

export const getters: GetterTree<RootState, RootState> = {
  // name: state => state.name,
}

export const mutations: MutationTree<RootState> = {
  SET_SEARCH_LIST: (state, data: any) => {state.search_list = data},
}

export const actions: ActionTree<RootState, RootState> = {
  async get_search_list({ commit }) {
    try {
      // @ts-ignore
      let res = await this.$axios.get(`/xz_server/search/getSearchList`)
      commit('SET_SEARCH_LIST', res.data)
    } catch (e) {
      console.log('SET_SEARCH_LISTe:', e)
    }
  },
}
