











import {Vue, Component, Prop, Model} from 'vue-property-decorator'

@Component
export default class CommonProgress extends Vue {
  @Prop({default: 16})
  progress_height!: number;
  @Model('change', {default: 0})
  percent!: number;
  public ball_left: number = 0
  public begin_clientX: number = 0
  public ball_active: boolean = false
  public line_id:string = 'id_' + Math.random()
  public line_width:number = 0

  get ball_height() {
    return typeof this.progress_height === 'string' ? this.progress_height : this.progress_height + 'px'
  }

  get rate(){
    let _rate:any = this.ball_left/this.line_width * 100
    return _rate.toFixed()
  }
  mounted(): void {
    document.body.addEventListener('mousemove', this.ball_mousemove)
    document.body.addEventListener('mouseup', this.ball_mouseup)
    const line:any = this.$refs.line
    this.line_width = line.offsetWidth
    console.log('this.line_width:',this.line_width)
    console.log('this.percent:',this.percent)
    this.ball_left = this.line_width * this.percent / 100
  }

  ball_mousedown(e: any) {
    e.preventDefault()
    this.ball_active = true
    this.begin_clientX = e.clientX - this.ball_left
  }

  ball_mousemove(e: any) {
    e.preventDefault()
    if (this.ball_active) {
      this.ball_left = e.clientX - this.begin_clientX
      if(this.ball_left<0){
        this.ball_left = 0
      }
      if(this.ball_left>this.line_width){
        this.ball_left = this.line_width
      }

      this.$emit('change',this.rate)
    }
  }

  ball_mouseup(e: any) {
    e.preventDefault()
    if (this.ball_active) {
      this.begin_clientX = e.clientX - this.ball_left
      this.ball_active = false
    }
  }

}
