











import {Vue, Component} from 'vue-property-decorator'

const CommonMessageProps = Vue.extend({
  props: {
    message: {
      type: String,
      default: 'message'
    },
    type: {
      type: String,
      default: 'info'
    },
    during: {
      type: Number,
      default: 3000
    }
  }
})
@Component
export default class CommonMessage extends CommonMessageProps {
  loaded: string = ''

  mounted(): void {
    setTimeout(() => {
      this.loaded = 'loaded'
    }, 0)
    setTimeout(() => {
      this.loaded = ''
    }, this.during)
    setTimeout(() => {
      this.remove()
    }, this.during + 500)

  }

  remove<T>() {
        throw new Error("Method not implemented.");
    }
}
